import React, { useEffect } from 'react'
import UserLayout from '@/app/Layout/UserLayout/UserLayout';
import DashboardLayout from '@/app/Layout/DashboardLayout/DashboardLayout';
import { getMyConnectionsRequest } from '@/config/redux/action/authAction/index';
import { useSelector, useDispatch } from 'react-redux';
import Style from "./my_connection.module.css";


const dispatch = useDispatch();

const authState = useSelector((state) => state.auth)
  useEffect (() =>{
      dispatch(getMyConnectionsRequest({token: localStorage.getItem("token")}));
}, [])

useEffect(() =>{
  if(authState.connectionRequest.length != 0) {
    console.log(authState.connectionRequest)
  }

}, [authState.connectionRequest])
  return (
    <UserLayout>
    <DashboardLayout>
      <h1>my connections page</h1>

      {authState.connectionRequest.length !=0 && authState.connectionRequest.map((user , index) =>{
        return (
          <div className={Style.userCard} key={index}>
            <div style={{ dispaly: "flex" , alignItems :"center" }}>
             <div className={Style.profilePicture}>
              <img src={`${BaseURl}/${user.userId.profilePicture}`} alt="" />
             </div>
             <div className={Style.userInfo}>
              <h3>{user.userId.name}</h3>
              <p>{user.userId.username}</p>
             </div>
            </div>

          </div>
        )
      })}
    </DashboardLayout>
    </UserLayout>
  );

export default MyConnectionsPage;